<template>
  <div id="footer">
    <div
      id="footer-links"
      v-for="(linkCol, index1) in friendLinks"
      :key="index1">

      
      <div class="footer-links-col">

        <p class="footer-title">{{ linkCol.title }}</p>

        <div
          class="footer-links-col-links"
          v-for="(link, index2) in linkCol.links"
          :key="index2">

          <a target="_blank" :href="link.href">{{ link.name }}</a><br>

        </div>

      </div>

    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import axios from 'axios'

export default defineComponent({
  name: 'Footer',
  components: {  },
  setup() {
    const friendLinks = ref([])

    axios.get('/data/links.json')
      .then((res) => {
        friendLinks.value = res.data
      })

    return {
      friendLinks
    }
  }
})
</script>

